import React from 'react';

const DefaultPlaceHolderImage = 'https://placehold.jp/aaaaaa/eeeeee/300x445.png?text=dummy';

function Movie({movie}) {
  const poster = movie.Poster === 'N/A' ? DefaultPlaceHolderImage : movie.Poster;

  return (
    <div className="Movie">
      <h3>{movie.Title}</h3>
      <div>
        <img src={poster} alt={`The movie titled: ${movie.Title}`} />
      </div>
      <p>({movie.Year})</p>
    </div>
  )
}

export default Movie;