import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTrashAlt } from '@fortawesome/free-regular-svg-icons';
import React, {useState} from 'react';
import Modal from './Modal';

function Todo () {

  const [todos, setTodo] = useState([
    {
      task: 'Learn React.js',
      isCompleted: false,
    },
  ]);
  const [task, setTask] = useState('');
  const [show, setShow] = useState(false);
  const [index,setIndex] = useState();

  const handleNewTask = (e) => {
    setTask(e.target.value);
  }
  const handleSubmit = (e) => {
    e.preventDefault();
    if(task === '') return;
    setTodo(todos => [...todos,{task, isCompleted: false}]);
    setTask('');
  }
  const handleRemoveTask = (index) => {
    const newTodos = [...todos];
    newTodos.splice(index, 1);
    setTodo(newTodos);
  }
  const handleUpdateTask = (index) => {
    let newTodos = todos.map((todo, todoIndex) => {
      if(todoIndex === index){
        todo.isCompleted = !todo.isCompleted
      }
      return todo;
    })
    setTodo(newTodos)
  }

  return (
    <>
      <Modal show={show} setShow={setShow} handleRemoveTask={handleRemoveTask} index={index}/>
      <h2>Todo</h2>
      <div className="TodoApp">
        <form onSubmit={handleSubmit} className="TodoForm">
          Add Task :<input type="text" placeholder="Add New Task" value={task} onChange={handleNewTask} className="TodoInput" />
        </form>
        <ul className="TodoList">
          {todos.map((todo, index) => (
            <li key={index} className="TodoItem">
              <input type="checkbox" onClick={() => handleUpdateTask(index)} />
              <p style={todo.isCompleted === true ? {textDecorationLine: 'line-through'}:{}}>{todo.task}</p>
              <span onClick={() => {setShow(true);setIndex(index)}}><FontAwesomeIcon icon={faTrashAlt} /></span>
            </li>
          ))}
        </ul>
      </div>
    </>
  )
}

export default Todo;