import React, { useState } from 'react';

function Search(props) {
  const [searchValue, setSearchValue] = useState('');

  const handleSearchInputChanges = (e) => {
    setSearchValue(e.target.value);
  }
  const resetInputField = () => {
    setSearchValue('');
  }
  const callSearchFunction = (e) => {
    e.preventDefault();
    props.search(searchValue);
    resetInputField();
  }

  return (
    <form>
      <input onChange={handleSearchInputChanges} value={searchValue} type="text" placeholder="Enter the movie" className="SearchField"  />
      <input onClick={callSearchFunction} value="SEARCH" type="submit" className="SubmitButton" />
    </form>
  )
}

export default Search;