import React,{ useEffect, useState } from 'react';

const CURRENT_WEAHER_API_URL = 'https://api.openweathermap.org/data/2.5/weather?q=';
const CURRENT_WEAHER_API_KEY = '&appid=49f792b6b990f5bd2c65da6fdb491e97';
const CURRENT_WEATHER_ICON_URL ='https://openweathermap.org/img/w';
const today = new Date();
const date = `${today.getFullYear()}-${today.getMonth()+1}-${today.getDate()}`;

function CityCard({city}) {
  const [weather, setWeather] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const CurrenWeatherApi = CURRENT_WEAHER_API_URL + city + CURRENT_WEAHER_API_KEY + '&units=metric';
    fetch(CurrenWeatherApi)
      .then(res => res.json())
      .then(jsonRes => {
        setWeather(jsonRes);
        setLoading(false);
      });
  }, []);

  if(loading) {
    return (
      <div className="LoadingBox">
        <div className="LoadingBorder">
          <div className="LoadingClip"></div>
        </div>
      </div>
    );
  }

  return (
    <div className={`CityCard ${weather.weather[0].main}`}>
      <div className="WeatherMainInfo">
        <div>
          <div className="MainInfoItem">
            <p className="ItemTitle">City Name</p>
            <p className="ItemText">{weather.name}</p>
          </div>
          <div className="MainInfoItem">
            <p className="ItemTitle">Weather Condition</p>
            <p className="ItemText">{weather.weather[0].main}</p>
          </div>
        </div>
        <div>
          <img src={`${CURRENT_WEATHER_ICON_URL}/${weather.weather[0].icon}.png`} alt="" className="WeatherImage"/>
        </div>
      </div>
      <div className="WeatherSubInfo">
        <div className="SubInfoItem">
          <p className="ItemTitle">Date</p>
          <p className="ItemText">{date}</p>
        </div>
        <div className="SubInfoItem">
          <p className="ItemTitle">Temperture</p>
          <p className="ItemText">{weather.main.temp}°C</p>
        </div>
        <div className="SubInfoItem">
          <p className="ItemTitle">Humidity</p>
          <p className="ItemText">{weather.main.humidity}%</p>
        </div>
      </div>
    </div>
  )
}

export default CityCard;