import { NavLink, useNavigate } from 'react-router-dom';
import { auth, provider } from '../../firebase';
import { useState } from 'react';

export function Login() {
  const navigate = useNavigate();
  const [error, setError] = useState('');
  const handleLoginSubmit = async (e) => {
    e.preventDefault();
    const {email, password} = e.target.elements;
    try {
      await auth.signInWithEmailAndPassword(email.value, password.value);
      navigate('/');
    } catch(error) {
      setError(error.message);
      // setError('The login attempt failed.\nEither the mail address or password is invalid.');
    }
  }
  const handleGoogleLogin = async (e) => {
    e.preventDefault();
    try {
      await auth.signInWithPopup(provider);
      navigate('/');
    } catch(error) {
      setError(error.message);
      // setError('The login attempt failed.\nEither the mail address or password is invalid.');
    }
  }

  return(
    <>
      <h2>Log In</h2>
      <form onSubmit={handleLoginSubmit} className="SignForm">
        {error && <p className="ErrorMessage">{error}</p>}
        <div>
          <label>mail address</label>
          <input name="email" type="email" placeholder="email" />
        </div>
        <div>
          <label>password</label>
          <input name="password" type="password" autoComplete="off" placeholder='password' />
        </div>
        <button className="SubmitButton">Login</button>
        <p className="AnotherFormLink">
          <NavLink to="/sign_up">Register here</NavLink>
        </p>
        <button onClick={handleGoogleLogin} className="SubmitButton GoogleLogin">Login with Google</button>
      </form>
    </>
  )
}