import React, { useState } from 'react';
import CityCard from './CityCard';

function Weather() {
  const [cities, setCity] = useState([
    'Tokyo', 
  ]);
  const [capital, setCapital] = useState('');

  const handleSelectChange = (e) => {
    setCapital(e.target.value);
  }
  const handleAddCity = (e) => {
    e.preventDefault();
    if(capital === '') return;
    setCity(city => [...city, capital]);
    setCapital('');
  }

  return (
    <>
      <h2>Weather</h2>
      <div className="WeatherApp">
        <form onSubmit={handleAddCity}>
          <input type="text" onChange={handleSelectChange} className="SearchField" value={capital}/>
          <input type="submit" value="Add City" className="SubmitButton" />
        </form>
        <p className="ExampleCities">Paris,Washington,Seoul,Canberra,Cairo…</p>
        {
          cities.map((city, index) => (
            <CityCard key={index} city={city} />
          ))
        }
      </div>
    </>
  )
}

export default Weather;