import { auth } from '../../firebase';
import { useNavigate } from 'react-router-dom';

export function Logout() {
  const navigate = useNavigate();
  const handleLogout = () => {
    auth.signOut();
    navigate('/login');
  }

  return(
    <>
      <h2>Logout</h2>
      <div className="LogoutPage">
        <button onClick={handleLogout}>Logout</button>
      </div>
    </>
  )
}