function Modal({show, setShow, handleRemoveTask, index}){
  if(show){
    return(
      <div className="Overlay">
        <div className="Content">
          <h3 className="ModalTitle">Delete Task？</h3>
          <p></p>
          <div className="ModalButtons">
            <button onClick={() => setShow(false)} className="CancelButton">Cancel</button>
            <button onClick={() => {handleRemoveTask(index);setShow(false)}} className="DeleteButton">Delete</button>
          </div>
        </div>
      </div>
    )
  }else{
    return null;
  }
  
}

export default Modal;