import { BrowserRouter, Routes, Route } from 'react-router-dom';
import PrivateHeader from './components/header/PrivateHeader';
import PublicHeader from './components/header/PublicHeader';
import Footer from './components/Footer';
import { Home } from './components/home/Home';
import Counter from './components/Counter';
import Todo from './components/todo/Todo';
import SearchMovie from './components/movie/SearchMovie';
import Weather from './components/weather/Weather';
import SignUp from './components/signup/SignUp';
import { Login } from './components/login/Login';
import { AuthProvider } from './context/AuthContext';
import { PrivateRoute } from './components/PrivateRoute';
import { PublicRoute } from './components/PublicRoute';
import { Logout } from './components/logout/Logout';
import './App.css';

function App() {
  return (
    <div className="App">
      <AuthProvider>
        <BrowserRouter>
          <Routes>
            <Route path="/" element={<PrivateHeader />} />
            <Route path="/counter" element={<PrivateHeader />} />
            <Route path="/todo" element={<PrivateHeader />} />
            <Route path="/search_movie" element={<PrivateHeader />} />
            <Route path="/weather" element={<PrivateHeader />} />
            <Route path="/logout" element={<PrivateHeader />} />
            <Route path="/sign_up" element={<PublicHeader />} />
            <Route path="/login" element={<PublicHeader />} />
          </Routes>
          <main>
            <Routes>
              <Route path="/" element={<PrivateRoute><Home /></PrivateRoute>} />
              <Route path="/counter" element={<PrivateRoute><Counter /></PrivateRoute>} />
              <Route path="/todo" element={<PrivateRoute><Todo /></PrivateRoute>} />
              <Route path="/search_movie" element={<PrivateRoute><SearchMovie /></PrivateRoute>} />
              <Route path="/weather" element={<PrivateRoute><Weather /></PrivateRoute>} />
              <Route path="/logout" element={<PrivateRoute><Logout /></PrivateRoute>} />
              <Route path="/sign_up" element={<PublicRoute><SignUp /></PublicRoute>} />
              <Route path="/login" element={<PublicRoute><Login /></PublicRoute>} />
            </Routes>
          </main>
          <Footer />
        </BrowserRouter>
      </AuthProvider>
    </div>
  );
}

export default App;
